import { FC, useEffect } from "react";
import { Link } from "react-router-dom";
import { Layout } from "src/components/layout/layout";
import { useChangeLanguage } from "src/hooks/use-change-language";
import { useAnalitics } from "src/hooks/use-analitics";
import { useStore } from "src/store/store";
import { findWarehouse } from "src/utils/find-warehouse";
import { findImg } from "src/utils/find-img";
import { useShallow } from "zustand/react/shallow";
import style from "./warehouse-schema-page.module.css";

const WarehouseSchemaPage: FC = () => {
  const { setHouseName, setHouseData } = useStore(
    useShallow(state => ({
      setHouseName: state.setHouseName,
      setHouseData: state.setHouseData,
    })),
  );

  const houseData = useStore(state => state.houseData);

  const { t } = useChangeLanguage();

  const query = findWarehouse(houseData);

  const url = query && findImg(query);

  useAnalitics();

  useEffect(() => {
    setHouseName(query?.name);
    return () => setHouseData(null);
  }, [setHouseName, setHouseData, query]);

  return (
    <Layout>
      <article className={style.schema}>
        <h1>{t("schema-warehouse")}</h1>
        <div className={style.schema__img}>
          <img src={url} alt={query?.name} />
        </div>
        <button className={style.schema__btn}>
          <Link to={`/${query?.query}`}>{t("close-btn")}</Link>
        </button>
      </article>
    </Layout>
  );
};
export default WarehouseSchemaPage;
